@import url(app.css);

/* Container and layout */
.register-section {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.register-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  width: 35%;
  background-color: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  position: relative;
}

.form-heading {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1rem;
}

/* Form layout */
.register-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Rows for first/last name and passwords */
.form-row {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

/* Name row */
.form-row.name-row {
  flex-wrap: nowrap;
}

/* Password row */
.form-row.password-row {
  flex-wrap: nowrap;
}

/* Inputs */
.form-input,
textarea,
select {
  font-size: 1rem;
  background-color: rgb(234, 234, 234);
  padding: 0.7rem 1rem;
  border: none;
  border-radius: 5px;
  font-family: var(--font-family);
  flex: 1;
  min-width: 0;
}

input[type="file"] {
  padding: 0.3rem 0.5rem;
}

.form-input:focus,
textarea:focus,
select:focus {
  outline: none;
}

/* Password wrapper */
.password-wrapper {
  position: relative;
  flex: 1;
}

.password-wrapper .form-input {
  width: 100%;
  padding: 0.5rem 2.5rem 0.5rem 1rem;
  /* top/right/bottom/left */
  font-size: 0.8rem;
  /* smaller text */
  border-radius: 5px;
  background-color: rgb(234, 234, 234);
  border: none;
}

.password-toggle {
  position: absolute;
  right: 0.8rem;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  font-size: 1.2rem;
  color: #333;
}

/* Buttons */
.btn.form-btn {
  padding: 0.8rem;
  border: none;
  border-radius: 5px;
  background-color: #114079;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn.form-btn:hover {
  background-color: #0f2c5e;
}

/* Back button */
.btn.back-btn {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  background-color: #115379;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 auto 1rem auto;
  display: block;
}

/* Login link */
.login-link {
  color: var(--darker-blue);
  text-decoration: underline;
  text-align: center;
  margin-top: 1rem;
  display: block;
}

/* Responsive */
@media (max-width: 900px) {
  .register-container {
    width: 50%;
  }
}

@media (max-width: 600px) {
  .register-container {
    width: 70%;
  }

  .form-row.name-row,
  .form-row.password-row {
    flex-direction: column;
    /* stack vertically on small screens */
  }
}

@media (max-width: 400px) {
  .register-container {
    width: 90%;
  }
}

.login-link {
  color: var(--darker-blue);
  text-decoration: underline;
  margin-left: 0.3rem;
  /* small space from preceding text */
  display: inline;
  /* keep on the same line */
}